    finishAuction() { //    
      if (!this.$session.exists()) {
        alert('Login first')
        this.$router.push('/login')
        return
      }

      if (1){ //           
        this.successAlert_finish()
        this.$bvModal.hide('modal-multi-17')
        this.$bvModal.hide('modal-multi-18')
      }
      else{
        this.failAlert_finish()
        this.$bvModal.hide('modal-multi-17')
        this.$bvModal.hide('modal-multi-18')
      }

